.catalog {
  display: flex;
  justify-content: center;
  padding-top: 50px;

  &__cards {
    display: flex;
    flex-wrap: wrap;
    max-width: 4 * $cardWidth + 3 * $cardMargin;
  }

  &__card {
    margin-right: $cardMargin;
    margin-bottom: $cardMargin;

    &:nth-child(4n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }
}

.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;

  &-block {
    &__image {
      width: 160px;
      height: 134px;
      margin-bottom: 40px;
    }

    &__title {
      @extend %weight_500;
      font-size: 12px;
      line-height: 18px;
      color: #060b35;
      margin-bottom: 4px;
    }

    &__code {
      @extend %weight_400;
      width: 100%;
      height: 14px;
      font-size: 10px;
      line-height: 14px;
      color: #616070;
      margin-bottom: 16px;
    }

    &__rating {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;
      width: 100%;
    }

    &__comments {
      @extend %weight_400;
      font-size: 10px;
      line-height: 14px;
      color: #060b35;
    }

    &__price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin-bottom: 16px;
    }

    &__price-text {
      @extend %weight_400;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    &__price-value {
      @extend %weight_700;
      font-size: 16px;
      line-height: 18px;
      color: #060b35;
    }

    &__button {
      display: flex;
      width: 100%;
    }

    &__button-link {
      @extend %weight_700;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 40px;
      background: $textColor;
      border-radius: 5px;
      font-size: 14px;
      line-height: 16px;
      text-transform: uppercase;
      text-decoration: none;
      color: #fff;
    }

    &__button-link:hover {
      background: #fff;
      border: 1px solid $textColor;
      border-radius: 5px;
      color: $textColor;
    }
  }
}
